const createSet = (start, stop) => {
    let arr = [];

    for (; start <= stop; start++) {
        arr.push(start);
    }

    return arr;
};

const isAPerfectSquare = (num) => {
    return (0 < num) && (0 === Math.sqrt(num) % 1);
};

const getProductOfSet = (arr) => {
    if (undefined == arr || 0 === arr.length) {
        return false;
    }

    let product = 1;

    arr.forEach((v) => {
        product *= v;
    });

    return product;
};

const getPowerSet = (arr) => {
    let ps = [
        []
    ];

    arr.forEach((v) => {
        var len = ps.length;

        for (let j = 0; j < len; j++) {
            let n = ps[j] ? [...ps[j], v] : v;
            ps = [...ps, ...[n]];
        }
    });

    return ps;
};


const findNumberOfPerfectSquareSubSets = (start, stop) => {
    let set = createSet(start, stop);
    let possibleSubSets = getPowerSet(set);

    let perfectSquareSubSets = possibleSubSets.reduce((arr, subset) => {
        let setProduct = getProductOfSet(subset);
        return (isAPerfectSquare(setProduct)) ? [...arr, subset] : [...arr];
    }, [])

    return perfectSquareSubSets.length;
};


export {
    findNumberOfPerfectSquareSubSets
};